export default class Filter {
    static $instance = ["$filter", ($filter) => {
        return new Filter($filter).filter;
    }];
    static $inject = ['$filter'];
    constructor(
        private $filter: ng.IFilterFilter
    ) {
        // console.log(this.$filter);
    }
    filter(input, obj) {
        console.debug(" ", input);
        console.debug(" ", obj);
        return input + "--";
    }
}




