
/**
 *    Qiniu  qiniu 
 */
export default class {
    constructor() { }
    /**
     *   qiniu 
     */
    Create() {
        return new qiniu();
    }
}
/**
 * qiniu
 */
class qiniu {
    config = {
        runtimes: 'html5,flash,html4',   //,
        browse_button: 'pickfiles',       //****
        uptoken_url: '/qiniu/UpToken',
        //AjaxupTokenUrl****
        // uptoken : '<Your upload token>',
        //uptoken_url, uptoken ,uptoken
        // unique_names: true,
        //  falsekeySDKkey
        // save_key: true,
        //  falseuptoken `sava_key`SDKkey
        domain: 'http://olt0mifi5.bkt.clouddn.com',
        //bucket ****
        // container: containerId,           //DOM IDbrowser_button
        max_file_size: '1000mb',         //
        // flash_swf_url: '../js/plupload/Moxie.swf',  //flash,
        filters: {
            // mime_types: [
            //     // extensions
            //     { title: "", extensions: "jpg,gif,png,bmp" }
            // ]
        },
        get_new_uptoken: false,
        unique_names: true,
        // log_level: 5,
        max_retries: 3,                   //
        // dragdrop: true,                   //
        // drop_element: 'editor-container',        //ID
        chunk_size: '4mb',                //
        auto_start: false,                 //
        init: {
            'FilesAdded': this.FilesAdded,
            'BeforeUpload': this.BeforeUpload,
            'UploadProgress': this.UploadProgress,
            'FileUploaded': this.FileUploaded,
            'Error': this.Error,
            'UploadComplete': this.UploadComplete
            // Key 
            //,
            // 'Key': function(up, file) {
            //     // key
            //     //  unique_names: false , save_key: false 
            //     var key = "";
            //     // do something with key here
            //     return key
            // }
        }
    }
    Qiniu: any
    /** */
    Start() {
        this.Qiniu.start();
    }
    /** */
    UploadInit() {
        console.log("----------config", this.config);
        return this.Qiniu = Qiniu.uploader(this.config);
    }
    FilesAdded(up, files) {
        console.log("FilesAdded  ");
    }
    BeforeUpload(up, file) {
        console.log("BeforeUpload  ");

    }
    UploadProgress(up, file) {
        console.log("UploadProgress  ");

    }
    FileUploaded(up, file, info) {
        console.log("FileUploaded  ");

    }
    Error(up, err, errTip) {
        console.log("Error  ");

    }
    UploadComplete() {
        console.log("UploadComplete");
    }
}


